.navbar {
  width: 100%;
  height: 80px;
  border-bottom: 1px solid #333;
  position: fixed;
  z-index: 2;
}

.navbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 1rem;
}

.navbar .nav-menu {
  display: flex;
}

.navbar li {
  padding: 1rem;
  text-transform: uppercase;
  font-size: 1.2rem;
  cursor: pointer;
}

.navbar li:hover {
  border-bottom: 2px solid #000;
  transform: scale(1.2);
}

.hamburger {
  z-index: 5;
  cursor: pointer;
}

.active {
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  transition: 0.7s;
  z-index: 5;
  top: 0;
  left: 0;
}

.nav-menu.active li {
  font-size: 2rem;
  color: #f8f8f8;
}

.nav-menu.active li:hover {
  color: var(--primary-color);
  transform: scale(1.2);
  transition: 0.3s ease;
  cursor: pointer;
}

@media screen and (max-width: 740px) {
  .navbar .nav-menu {
    position: absolute;
    left: -200%;
  }

  .navbar .active {
    left: 0;
  }
}
